.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  font-size: 3em;
  color: #555555;
  cursor: pointer;
}

.scroll-to-top.hidden {
  opacity: 0;
}

.scroll-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  font-size: 2em;
  color: #555;
  cursor: pointer;
}
